import styled from 'styled-components'
import React, { useRef } from "react"
import { Canvas, useFrame } from "react-three-fiber"
import { Html } from "@react-three/drei"
import { color } from '@style/color'

function Dodecahedron({ text, sphereColor, ...props }) {
  return (
    <mesh {...props}>
      <sphereGeometry attach="geometry" args={[1.5, 32, 32]}/>
      <meshStandardMaterial roughness={1} emissive={sphereColor}/>
      <Html scaleFactor={10}>
        <div class="content">
          {text}
        </div>
      </Html>
    </mesh>
  )
}

function Content() {
  const ref = useRef()
  useFrame(() => (ref.current.rotation.x = ref.current.rotation.y = ref.current.rotation.z += 0.01))
  return (
    <group ref={ref}>
      <Dodecahedron text="" position={[-2, 2, 0]} sphereColor={color.primary.S300}/>
      <Dodecahedron text="" position={[0, 0, -3]} sphereColor={color.secondary.S300}/>
      <Dodecahedron text="" position={[3, 1.5, 0]} sphereColor={color.support.S300}/>
    </group>
  )
}

export default function App() {
  return(
    <Container>
      <Canvas camera={{ position: [-2, 0, 4.5] }}>
        <pointLight position={[10, -10, -10]}color={color.deco.skyblue} />
        <pointLight position={[10, 10, -10]} color={color.deco.mustard} />
        <pointLight position={[-10, -10, 10]} color={color.primary.S300} />
        <Content />
      </Canvas>
    </Container>
  )
}

const Container = styled.div`
  position: absolute;
  top: 0;
  left: 0;
  z-index: 5;
  width: 100%;
  -webkit-mask-image: -webkit-gradient(linear, left top, left bottom, 
    from(rgba(0,0,0,1)), to(rgba(0,0,0,0)));
  canvas{
    height: 150%;
    width: 100%;
  }
`