import React from 'react'

import { Headline, Subline, Caption} from '@style/typography'
import { Container, Section, GridCol, Box } from '@style/global'

class Masthead extends React.Component {
  render() {
    return (
      <Container>
        <Section>
          <GridCol col={{ desktop: "9fr 3fr" }} gap="100px">
            <Box>
              <Headline>Impact ETF 101</Headline>
              <Subline>
                A quick journey to sustainability-themed exchange-traded funds (ETFs)
              </Subline>
            </Box>
          </GridCol>
          <GridCol col={{desktop: "9fr 3fr"}} gap="50px">
              <Box>
                <p>Impact ETFs are exchange traded funds (ETFs) which aims to create positive impact on our society and planet along with financial return. This visual journey will walk you through the basics. You may have heard the similar terms such as sustainable investing and socially-responsible investing. No matter which term we are referring is, for the impact assessment, Environment, Society, and Governance (ESG) is the most used umbrella concept by the industry and academia.</p>

                <p>As such, ESG, literally the three core values to pursue through impact investment, is a good start for the beginners. ESG is promoted by the United Nation's Principles for Responsible Investment. It was adopted by the industry leading sustainability data providers such as MSCI, Sustainalytics, and Morningstar for the construction of their research and rating methodologies.</p>

                <p>Based on the ESG data from the providers, the issuers of the impact ETFs may either integrate ESG as the overall theme of their portfolio or as part of their investment strategy. They screen the potential companies and exclude those who breach their ESG criterion. They may even execute their shareholder rights (e.g. voting) to alter the behaviors of the companies for sustainable pursuit. </p>
              </Box>
              <Box margin="10px 0 0 0">
                <Caption>
                  This is an interest project solely designed and developed by data interface designer Ansin Lau. The information about ESG and impact investment were part of his master's thesis in design and sustainability at Aalto University, Finland.
                </Caption>
              </Box>
              </GridCol>
        </Section>
      </Container>
    )
  }
}

export default Masthead
