import React from 'react'
import { VictoryChart, VictoryTooltip, VictoryVoronoiContainer, VictoryStack, VictoryBar } from 'victory';
import { color } from '@style/color'
import { globalType } from '@style/typography'

const data = [
  { x: "non-ESG-mandated", y: 	[{x: "2012", y: 29.6}, {x: "2014", y: 30.2}, {x: "2016", y:  31.6}, {x: "2018", y: 34.6}, {x: "2025", y: 34.5},], color:	`${color.grayscale.S500}`},
  { x: "ESG-mandated", y: 	[{x: "2012", y: 3.7}, {x: "2014", y: 6.6}, {x: "2016", y: 8.7}, {x: "2018", y: 12.0}, {x: "2025", y: 34.5}], color:	`${color.primary.S500}`},
]

export function  BGrowth (){
    return (
      <VictoryChart height={400} width={400}
        domainPadding={{ x: 30, y: 10 }}
        style= {[
          {fill: `${color.grayscale.S800}` , fontFamily: `${globalType.font.secondary};`, fontSize: 10}
        ]}
        containerComponent={
          <VictoryVoronoiContainer
            voronoiDimension="x"
            labels={({ datum }) => `y: ${datum.x}`}
            labelComponent={
              <VictoryTooltip
                cornerRadius={0}
                flyoutStyle={{ fill: "white" }}
              />}
          />}
      >
        <VictoryStack
          colorScale={[`${data[0].color}`,`${data[1].color}`]}
        >
          {
            data.map((data => 
            <VictoryBar
              data={data.y}
            />))
          }
        </VictoryStack>
      </VictoryChart>
    );
  }